import React, { useEffect, useState, useCallback } from "react";
import { CircularProgress } from "@material-ui/core";
import { Pagination } from "@material-ui/lab";
import { makeStyles } from "@material-ui/core/styles";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import queryString from "qs";

import { fetchUsers } from "../../redux/actions";
import ViewUser from "./ViewUser";
import { usePagination } from "../../hooks";

const useStyles = makeStyles(() => ({
    root: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        minHeight: "calc(100vh - 32px)",
        backgroundColor: "white",
        width: "calc(100vw - 16px - 256px - 16px - 16px - 400px - 16px - 16px)",
        marginLeft: 256 + 16 + 16,
        borderRadius: 16,
        paddingTop: 32,
        paddingBottom: 32,
        position: "relative",
    },
    pageTitle: {
        fontSize: 40,
        marginTop: 32,
        fontWeight: 600,
    },
    progress: {
        width: 40,
        height: 40,
    },
    seperator: {
        height: 0.5,
        width: "calc(100% - 96px)",
        backgroundColor: "#343A4030",
        marginTop: 16,
        marginBottom: 16,
        marginLeft: "auto",
        marginRight: "auto",
    },
    users: {
        marginBottom: 24,
        width: "100%",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
    },
    pagination: {
        position: "absolute",
        bottom: 16,
    },
}));

const DEFAULT_ROWS_PER_PAGE = 20;

const ViewUsers = () => {
    const classes = useStyles();
    const { list, fetching, totalPages } = useSelector((state) => state.users);
    const dispatch = useDispatch();
    const { generateURL, page, setPage, rowsPerPage } = usePagination();

    useEffect(() => {
        dispatch(fetchUsers({ page, limit: rowsPerPage }));
    }, [dispatch, page, rowsPerPage]);

    const onPageChange = (event, newPage) => {
        setPage(newPage - 1);
        generateURL(newPage - 1, rowsPerPage);
        window.scrollTo(0, 0);
    };

    const renderUsers = () =>
        list.map((item) => (
            <div key={item.id} className={classes.users}>
                <ViewUser
                    firstName={item.firstName}
                    lastName={item.lastName}
                    userName={item.userName}
                    emailAddress={item.emailAddress}
                    pictureURL={item.pictureURL}
                />
                <div className={classes.seperator} />
            </div>
        ));

    return (
        <>
            <div className={classes.root}>
                {!fetching && list.length > 0 && renderUsers()}
                {fetching && <CircularProgress className={classes.progress} />}
                {!fetching && (
                    <Pagination
                        className={classes.pagination}
                        color="secondary"
                        count={totalPages}
                        page={page + 1}
                        onChange={onPageChange}
                    />
                )}
            </div>
        </>
    );
};

export default ViewUsers;
